export default {
	data() {
		return {
			list:[]
		};
	},
	onLoad() {
		uni.showLoading({
			title: '课程',
			mask: false
		});
		uni.request({
			url: '../../static/data/video.json',
			method: 'GET',
			data: {},
			success: res => {
				this.list = res.data.data
				uni.hideLoading()
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods:{
		toplay(e) {
			let id = e.currentTarget.dataset.id
			uni.navigateTo({
				url: '../player/player?id=' + id
			})
		}
	}
}
